<template>
    <div>
        <main>
        <div class="title">
            <h1>Logs d'action</h1>
        </div>
        <div class="card content users">
            <button class="btn" @click="$router.push('/dashboard')">Retour</button>
            <input class="btn" v-model="search" placeholder="Rechercher par utilisateur, date, etc" title="Rechercher par utilisateur, date, etc"/>
            <h2>Logs</h2>
            <BarLoader style="margin: 0 auto; border-radius:5px" v-if="logsLoading"></BarLoader>
            <div v-else-if="logs.length!==0">
                <p v-for="log in getLogs" :key="log.id">
                    <sub>{{log.created_at}}</sub><br>
                    <b>{{log.action}} - {{log.user}}</b><br>
                    <span>{{log.content}}</span>
                </p>
            </div>
            <span v-else>Aucun log</span>
        </div>
        </main>
    </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'

export default {
    data () {
        return {
            search: ''
        }
    },
    computed: {
        ...mapGetters('users',[
            'currentUser'
        ]),
        ...mapGetters('logs',[
            'logs',
            'logsLoading'
        ]),
        getLogs: function() {
            if(this.search=='' || this.search == ' ') return this.logs.slice().reverse()            
            return this.logs.filter(log=>log.user==this.search||log.action==this.search||log.content.includes(this.search)||log.created_at.includes(this.search))
        }
    },
    methods: {
        ...mapActions('logs',[
            'fetchLogs'
        ]),
    },
    created () {
        this.fetchLogs();
    }
}
</script>
<style scoped>
.btn {
    outline: none;
    transition: all .3s;
    user-select: none;
    display: inline-block;
    border-radius: 7px;
    border: 1px solid var(--b3);
    font-size: 15px;
    margin: 10px 15px 0 0;
    padding: 10px 16px;
}
button {
    cursor: pointer;
    background: var(--b3);
    color: var(--b8);
}

button.outline {
    background: none;
    color: var(--b3);
}
button:hover { 
    border: 1px solid var(--b2);
    background: var(--b2);
}
button.outline:hover { 
    border: 1px solid var(--b3);
    background: var(--b3);
    color: var(--b8);
}
  
</style>